body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cart-items-table {
  width: 550px;
  border-collapse: collapse;
}

.cart-items-table th,
.cart-items-table td {
  padding: 5px 10px;
}

.cart-items-table th {
  text-align: left;
  white-space: nowrap;
  border-bottom: 1px solid #cccccc;
  cursor: pointer;
}

.cart-items-table th.active {
  background-color: #cccccc;
}

.cart-items-table th.active::after {
  display: inline-block;
  margin-left: 4px;
}

.cart-items-table th.active.asc::after {
  content: '\2193';
}

.cart-items-table th.active.desc::after {
  content: '\2191';
}
.qty-switcher {
  display: -webkit-flex;
  display: flex;
}

.qty-switcher__value {
  display: inline-block;
  padding: 0 5px;
}
.product-form {
  width: 400px;
  margin-top: 20px;
  padding: 20px;
}

.product-form input {
  display: block;
  margin: 5px 0 20px;
}
.cart {
  display: -webkit-flex;
  display: flex;
}

.cart-actions {
  padding: 40px;
}
